@import "variables";

body{
  .contentAddres {
    display: flex;
    iframe {
      width: 60%;
      height: 600px;
    }
    &__contactForm {
      width: 40%;
      display: flex;
      flex-wrap: wrap;
      background-color: #176975;
      .comentary {
        margin: 45px 0 0 45px;
        input {
          padding-left: 15px;
          font: 1.125em/45px @sansregular;
          color: #828282;
          border: 1px solid #176975;
          border-radius: 10px;
          outline: none;
        }
        input[type*="text"] {
          margin: 0 5px 10px;
          width: 245px;
          height: 45px;
        }
        textarea {
          margin: 0 5px 10px;
          padding-left: 15px;
          width: 515px;
          height: 100px;
          font: 1.125em/45px @sansregular;
          color: #828282;
          border: 1px solid #176975;
          border-radius: 10px;
          outline: none;
          resize: none;
        }
        button {
          margin: 20px 0 0 5px;
          width: 165px;
          height: 45px;
          font: 1.125em/40px @sansregular;
          color: #fff;
          text-align: center;
          background: transparent;
          border: 1px solid #fff;
          border-radius: 10px;
          outline: none;
          &:hover {
            cursor: pointer;
          }
        }
      }
      .contacts {
        margin: 0 0 0 45px;
        width: 70%;
        display: flex;
        justify-content: space-between;
        p {
          font: 1.125em/1.25em @sansregular;
          color: #fff;
          a {
            color: #fff;
            text-decoration: none;
            &:hover {
              text-decoration: underline;
            }
          }
        }
      }
      .social {
        margin: 0 0 0 45px;
        width: 70%;
        display: flex;
        justify-content: space-between;
        ul {
          display: flex;
          justify-content: space-between;
          a {
            margin: 0 5px;
            display: flex;
            justify-content: center;
            align-items: center;
            width: 50px;
            height: 50px;
            font-size: 1.5em;
            color: #176975;
            text-decoration: none;
            border-radius: 50px;
            i{
              font-size: 2em;
              color: #fff;
            }
            &:hover{
              color: #fff;
              background-color: @hoverColor;
              transition: .5s;
            }
          }
        }
        p {
          color: #fff;
          text-align: right;
          font: 1em/1.125em @sansbold;
          span:nth-child(1) {
            font: .875em/1em @sansregular;
          }
        }
      }
    }
  }
}